import React from 'react';
import { render } from 'react-dom';
import { Title } from './Components/Title';
import { Table } from './Components/Table';
import { w3cwebsocket } from 'websocket';

var IoT = React.createClass({displayName: "IoT",
	getInitialState: function() {
		return {
			history: []
		}
	},

	handleData: function(payload) {
		var data = JSON.parse( payload.data );
		var history = this.state.history;

		history.push({
			temperature: data.temperature
		});

		this.setState({ history: history });
	},

	componentDidMount: function() {
		var websocket = new w3cwebsocket(this.props.uri, '');

		websocket.onmessage = this.handleData;
	},

	render: function() {
		return (
			React.createElement("div", null, 
    			React.createElement(Table, {history: this.state.history})
			)
		);
	}
});

render(
	React.createElement("div", null, 
		React.createElement(IoT, {uri: "ws://192.168.11.10:8000/object/123456/viewer"})
	),
    document.getElementById('content')
);  